@import url(https://fonts.googleapis.com/css?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Uncomment to use only in development mode */

/* * {
    background: rgba(255, 0, 0, .1);
    box-shadow: 0 0 0 1px red;
} */

body {
    background-color: #fff;
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
    outline: none;
    display: flex;
    flex-direction: column;
    justify-content: center !important;
    align-items: center !important;
}

.inputFieldContainer input:focus,
.inputFieldContainer select:focus,
.inputFieldContainer textarea:focus,
.inputFieldContainer button:focus
.inputFieldContainer span:focus{
    outline: 0;
}

div:focus .quickReply {
    outline: 0;
    border: 1px dotted #333;
    background-color: #58d358;
    -webkit-tap-highlight-color: transparent;
    /* Removes the blue highlight when button tapped on mobile */
}

.containerEunit {
    background-color: inherit;
    outline: 0;
}

.botIcon {
    position: fixed;
    left: -200px;
    top: 180px;
    width: 400px;
}

.chatbotOutput {
    background-color: rgba(255, 255, 255, 0.7);
    box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.25);
    text-align: left;
    font-family: Ubuntu, sans-serif;
    border: 0.1pt solid lightgray;
    position: relative;
    justify-content: left;
    font-size: 20px;
}

/* Unrecognized devices START */

.botOutputsContainer>div:last-child {
    margin: 0 0 350px 0;
    /* Gives the last .chatbotOutput (chat bubble) some space below; I guess for scrolling? ðŸ™‚ */
}

.botOutputsContainer {
    height: 100vh;
    min-height: 100vh;
    max-height: 100vh;
    overflow-y: scroll;
    scroll-behavior: smooth;
    position: fixed;
    left: 20%;
    top: 0;
    right: 5%;
    padding: 0;
    margin: 0;
    width: 100%;
}

.chatbotOutput {
    width: 65vw;
    max-width: 800px;
    line-height: 36px;
    border-radius: 10px;
    padding: 45px;
    display: inline-block;
    margin: 0 0 14px 0;
}


.quickReplyContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    position: fixed;
    left: 25%;
    bottom: 15%;
    width: 40%;
    word-wrap: normal;
    white-space: nowrap;
    padding: 3px;
    border-radius: 10px;
    margin: 15px auto;
    background-color: rgba(255, 255, 255, 0.7);
}

.quickReply {
    font-size: 18px;
    height: 32px;
    border-radius: 22px;
    margin: 25px 10px 10px;
    padding: 12px 18px;
    line-height: 60px;

}

/* Unrecognized devices END */

.quickReply {
    text-align: center;
    font-family: Ubuntu, sans-serif;
    text-decoration: none;
    text-transform: capitalize;
    outline: none;
    cursor: pointer;
    font-style: normal;
    font-weight: 400;
    color: rgb(0, 0, 0);
    background-color: #72b352;
    border: 1px solid #f5f5f5;
    border-image: none;
    border-image: initial;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=80)";
    filter: alpha(opacity=80);
    opacity: 0.8;
    -moz-opacity: 0.8;

}

.quickReply:hover {
    background-color: #67AB67;
}

.inputFieldContainer {
    position: fixed;
    margin-left: 5%;
    margin-right: 5%;
    max-width: 800px;
    width: 70%;
    left: 20%;
    bottom: 10%;
}

.inputField {
    z-index: 9999;
    font-family: Ubuntu, sans-serif;
    font-size: large;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 12px 12px 16px 0 rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    margin-right: 4rem;
    padding-left: 15px;
    width: 100%;
    height: 50px;
}

.inputFieldContainer input {
    padding: 5px;
}

.inputFieldContainer button {
    z-index: 9999;
    position: absolute;
    right: -10px;
    top: 5px;
    width: 70px;
    height: 45px;
    -moz-appearance: none;
    border: none;
    background: #ededed;
    border-radius: 3px;
    padding: 10px;
    margin: 5px;
    text-transform: capitalize;
    transition: all .2s;
    cursor: pointer;
}

.inputFieldContainer button:hover {
    background-color: #67AB67;
    color: white;
}

.inputFieldContainer button:active {
    background-color: #468546;
    color: white;
}

.undoIcon {
    z-index: 9999;
    position: fixed;
    top: 15%;
    left: 15%; /* todo: different for different screen sizes */
    font-size: xx-large;
    color: #67AB67;
    cursor: pointer;
    border-radius: 100%;
    border: 1px solid #67AB67;
    padding: 15px;
    background-color: rgba(255, 255, 255, 0.9);
}

.onClickAnimation {
    background-color: #67AB67 !important;
}

.conversationEnd {
    background-color: rgba(255, 255, 255, 0.7);
    box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.25);
    text-align: left;
    font-family: Ubuntu, sans-serif;
    border: 0.1pt solid lightgray;
    position: relative;
    justify-content: left;
    font-size: 20px;
    width: 65vw;
    max-width: 800px;
    line-height: 36px;
    border-radius: 10px;
    padding: 45px;
    display: inline-block;
    margin: 0 0 14px 0;
}

.btnRestartConversation {
    margin-top: 20px !important;
    width: 120px !important;
    height: 80px !important;
    font-size: medium !important;
}

@media only screen and (max-height: 600px) {
    .quickReplyContainer {
        position: fixed;
        bottom: 20%;
        width: 70%;
    }
}

/* Smaller Smarthphones */
@media only screen and (max-width: 480px) {

    .botOutputsContainer>div:last-child {
        margin: 0 0 600px 0;
        /* Gives the last .chatbotOutput (chat bubble) some space below; I guess for scrolling? ðŸ™‚ */
    }

    .botOutputsContainer {
        left: 5%;
        top: 0;

    }

    .chatbotOutput {
        min-width: 220px;
        width: 70vw;
        line-height: 35px;
        padding: 14px 28px;
        display: inline-block;
        margin: 0 14px 14px 0;
    }

    .inputFieldContainer {
        position: fixed;
        margin-left: 5%;
        margin-right: 5%;
        max-width: 800px;
        width: 75%;
        left: 13%;
        bottom: 5%;
    }

    .quickReplyContainer {
        position: fixed;
        left: 5%;
        bottom: 13%;
        width: 70%;
    }

    .quickReply {
        font-size: 17px;
        height: 28px;
        border-radius: 20px;
        margin: 25px 6px 6px;
        padding: 12px 15px;
        line-height: 58px;
    }

    .undoIcon {
        bottom: 6%;
        left:2%;
        top:auto;
        font-size: larger;
        margin-right: 5px;
    }

    .conversationEnd {
        position: fixed !important;
        width: 43vw;
        line-height: 28px;
        border-radius: 10px;
        padding: 25px;
        display: inline-block;
        margin: 0;
        left:5% !important;
        top:79% !important;
        font-size: medium !important;
    }

    .btnRestartConversation {
        position: fixed !important;
        right:2% !important;
        top: 77% !important;
    }
}

